<template>
    <div>
        <h4></h4>
        {{state}}
         : <textarea rows="6" v-model="state.content" /><br />
        <button @click="handleInsert"></button>
    </div>
</template>

<script>
import { reactive } from '@vue/reactivity'
import axios from 'axios';
import { useRoute} from 'vue-router';
export default {
    setup () {
        const route = useRoute();
        const state = reactive({
            receiver : route.query.receiver,
            token : sessionStorage.getItem("TOKEN"),
            content : ''
        });

        const handleInsert = async () => { // 
            const url = `/COCO/api/chat/insert.json?receiver=${state.receiver}`;
            const headers = {"Content-Type":"application/json", "TOKEN" : state.token};
            const body = {content : state.content};
            const {data} = await axios.post(url, body, {headers});
            console.log(data);
            if(data.status === 200){
                alert(' !');
            }
        };

        return {state, handleInsert};
    }
}
</script>

